var _self;
export default {
	data() {
		return {
			//模态框状态
			showFlag: false,
			// 省份列表
			addressd1: [],
			// 城市列表
			addressd2: [],
			// 地区列表
			addressd3: [],
			//省份id 默认为64，可根据想要默认展示的id自行更改
			co: '65',
			//默认省份名称
			coname: '',
			coObj:{},
			//城市id 默认为64，可根据想要默认展示的id自行更改
			ct: '64',
			//默认城市名称
			ctname: '',
			ctObj:{},
			//地区id 默认为575，可根据想要默认展示的id自行更改
			cs: '575',
			//默认地区名称
			csname: '',
			csObj:{}
		};
	},
	props: {
		//载入的标签数据
		addressd: Array
	},
	mounted() {
		_self = this;
		//默认获取省份列表
		this.getadd(1, 0);
		//默认获取城市列表
		this.getadd(2, this.co);
		//默认获取地区列表
		this.getadd(3, this.ct);
		// 声明默认地址,并传送给父组件
		this.emitData();

	},
	methods: {
		//呼出模态框
		showAddress() {
			// 呼出模态框
			this.showFlag = true
		},
		// 关闭模态框
		hideAddress() {
			// 关闭模态框
			this.showFlag = null;
			// 声明默认地址,并传送给父组件
			this.emitData();

		},
		// 声明默认地址,并传送给父组件
		emitData() {
			var data = {
				province: _self.coname,
				provinceID:_self.co,
				city: _self.ctname,
				cityID:_self.ct,
				district: _self.csname,
				districtID:_self.cs
			}
			_self.$emit("changes", data);
		},
		// 模态框地址点击赋值并获取下一级
		// --flag--- 1(省份点击);2(城市点击);3(地区点击)；
		// --id----(点击的地址id);
		// --name--(点击的地址名称);
		clickAddress(flag, id, name) {
			//判断点击的状态
			switch (flag) {
				case 1:
					_self.coname = name;
					_self.co = id;
					_self.getadd(2, id);
					break;
				case 2:
					_self.ctname = name;
					_self.ct = id;
					_self.getadd(3, id);
					break;
				case 3:
					_self.csname = name;
					_self.cs = id;
					_self.emitData();
					_self.hideAddress();
					break;
				default:
					return;
			}

		},
		
		
					getadd(flag, id) {
						uni.request({
							url: this.$store.state.url+'Districts', //仅为示例，并非真实接口地址。
							data: {
								parent: id,
								userId:49,
								owner:18
							},
							success: function(res) {
								switch (flag) {
									case 1:
										_self.addressd1 = res.data.data;
										
										break;
									case 2:
										_self.addressd2 = res.data.data;
										_self.ctname =res.data.data[0]?res.data.data[0].name:'';
										_self.ct =res.data.data[0]?res.data.data[0].id:1208;
										_self.getadd(3, _self.ct);
										break;
									case 3:
										_self.addressd3 =res.data.data;
										_self.csname = res.data.data[0]?res.data.data[0].name:'';
										_self.cs = res.data.data[0]?res.data.data[0].id:1220;
										break;
									default:
										return;
								}
							}
						})

				},
		
	}
}

